html, body {
  margin: 0;
  font-family: Roboto, Arial, Helvetica Neue, sans-serif;
}

:root {
  --active-collor: #00acdc;
}

[data-qa="hover"]:hover {
  color: var(--active-collor);
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-weight: 500;
  display: flex;
}

.header__link {
  width: 40px;
  height: 40px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  letter-spacing: 0;
  height: 60px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.is-active {
  color: var(--active-collor);
}

.is-active:after {
  content: "";
  background-color: var(--active-collor);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
